"use client";

import { useParams } from "next/navigation";
import { createSupabaseServer } from "@/lib/supabaseServer";
import { useEffect, useState } from "react";
import ReplayEmbed from "@/components/ReplayEmbed";

export default function WeekPage() {
  const params = useParams();
  const id = params.id;
  const week = params.week;

  const [replays, setReplays] = useState<any[]>([]);
  const [selectedBattleLog, setSelectedBattleLog] = useState<string | null>(null);

  useEffect(() => {
    const fetchReplays = async () => {
      const supabase = createSupabaseServer();
      const { data } = await supabase
        .from("replays")
        .select("id, team1, team2, file_url")
        .eq("division", Number(id))
        .eq("week", Number(week));

      setReplays(data || []);
    };

    fetchReplays();
  }, [id, week]);

  return (
    <div style={{ display: "flex", padding: 40, gap: 20 }}>
      {/* Left: list of replays */}
      <div style={{ width: "300px" }}>
        <h1>Division {id} â€” Week {week}</h1>
        {!replays.length && <p>No replays uploaded yet.</p>}
        {replays.map((r) => (
          <div key={r.id} style={{ marginBottom: 10 }}>
            <strong>{r.team1} vs {r.team2}</strong>
            <br />
            <button
              style={{ marginTop: 4, padding: "4px 8px" }}
              onClick={() => setSelectedBattleLog(r.file_url)}
            >
              View Replay
            </button>
          </div>
        ))}
      </div>

      {/* Right: interactive replay */}
      <div style={{ flex: 1, border: "1px solid #ccc", height: "600px" }}>
        {selectedBattleLog ? (
          <ReplayEmbed battleLog={selectedBattleLog} />
        ) : (
          <p style={{ padding: 20 }}>Click a replay to view it here.</p>
        )}
      </div>
    </div>
  );
}